.form-holder{
    background-color: white;
    width: fit-content;
    height: fit-content;
    border-radius: 10px;
}
.form-holder-dark{
    background-color: gainsboro;
    width: fit-content;
    height: fit-content;
    border-radius: 10px;
}
.theme-dark{
    background-color:rgb(249, 249, 249, 0.7);
    width: fit-content;
    height: fit-content;
    border-radius: 10px;
}
.input {
    display: grid;
    gap: 3rem;
    justify-items: center;
    margin-top: 2.5rem;

    
    /* border-radius: 10px; */
    
}
.cancel{
    background-color: red;
    padding: 5px 10px;
    cursor: pointer;
    width: 30px;
    float: right;
}
.input input{
    background: 0;
    /* margin-top: 3.2rem; */
    border: none;
    outline: none;
    border-bottom: 1px solid black;
    width: 390px;
    margin: 0 2rem;

}
.incorrectpassword{
    margin-top: -40px;
    margin-bottom: 0;
}
.incorrectpassword p{
    margin-left: 20px;
    color: red;

}
button{
    margin-top: 1rem;
    border: none;
    outline: none;
    background-color: cyan;
    margin-left: 40%;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 2rem;
}
input::placeholder{
    color: black;
}
.cancel:hover{
    opacity: 0.4;
}
@media(max-width:500px){
    .form-holder{
        background-color: white;
        width: 80%;
        height: fit-content;
        border-radius: 10px;
    }
    .form-holder-dark{
        background-color: gainsboro;
        width: 80%;
        height: fit-content;
        border-radius: 10px;
    }
}
@media(max-width:500px){
    .form-holder{
        background-color: white;
        width: 70%;
        height: fit-content;
        border-radius: 10px;
    }
    .form-holder-dark{
        background-color: gainsboro;
        width: 70%;
        height: fit-content;
        border-radius: 10px;
    }
    .input input{
        background: 0;
        /* margin-top: 3.2rem; */
        border: none;
        outline: none;
        border-bottom: 1px solid black;
        width: 200px;
        margin: 0 2rem;
    
    }
}